export default{
    rank:0,//0未登录，1普通用户，2会员，3超级管理员
    isAdmin:false,
    setIsAdmin(a){
        this.isAdmin = a;
    },
    setRank(a){
        this.rank = a;
        if(a ==3)this.setIsAdmin(true);
        else{this.setIsAdmin(false)}
    }
}


